import { Component, OnInit } from '@angular/core';
import { Contact } from '../../model/model.contact';
import { ActivatedRoute, Router } from '@angular/router';
import { ContactsSerivce } from '../../services/contacts.service';

@Component({
  selector: 'app-edit-contact',
  templateUrl: './edit-contact.component.html',
  styleUrls: ['./edit-contact.component.css']
})
export class EditContactComponent implements OnInit {
  mode:number=1;
  contact:Contact=new Contact();
  idContact:number;

  constructor( public activatedRoute:ActivatedRoute,
               public contactsSerivce:ContactsSerivce,
               public router:Router
) {
    this.idContact= activatedRoute.snapshot.params['id'];    
   }

  ngOnInit() {
    this.contactsSerivce.getContact(this.idContact)
    .subscribe(
      data=>{
        this.contact=data;
      },err=>{
        console.log(err);
      }
    )
  }
  updateContact(){
    this.contactsSerivce.updateContact(this.contact)
    .subscribe(
      data=>{
        alert("mise à jour effectuée");
        console.log(data);
        this.router.navigate(['contacts']);
      },err=>{
        alert("Problème");
        console.log(err);
      }
    )
  }


}



